<script>
import OpinionatedTable from './outputFormats/OpinionatedTable.vue';
import Csv from './outputFormats/Csv.vue';
import Json from './outputFormats/Json.vue';

export default {
  functional: true,
  render(createElement, context) {
    function appropriateFormatter(outputFormat) {
      switch (outputFormat) {
        case 'csv': return Csv;
        case 'json': return Json;
        default: return OpinionatedTable;
      }
    }

    return createElement(
      appropriateFormatter(context.props.format),
      context.data,
      context.children,
    );
  },
};
</script>
